import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-service',
  templateUrl: './service.component.html',
  styleUrls: ['./service.component.scss']
})
export class ServiceComponent implements OnInit {

  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private vhAuth: VhAuth,
    private languageService: LanguageService
  ) { }


  ngOnInit(): void {
  }

  gotoServicesList() {
    if (this.vhAuth.checkMyPermission('service_enable_manage_service')) {
      this.router.navigate(['/cafe/dashboard/management/service/service-list']);
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoServiceCategory() {
    if (this.vhAuth.checkMyPermission('service_enable_manage_category')) {
      this.router.navigate(['/cafe/dashboard/management/service/service-category']);
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
}
