
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ServiceComponent } from './service.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';

const routes: Routes = [
  {
    path: '',
    component:  ServiceComponent
  }, 
  {
    path: 'service-list',
    loadChildren: () => import('./services-list/services-list.module').then(m => m.ServicesListModule)
  },
  {
    path: 'service-category',
    loadChildren: () => import('./service-category/service-category.module').then(m => m.ServiceCategoryModule)
  }, 
]

@NgModule({
  declarations: [ServiceComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class ServiceModule { }
